* {
    box-sizing: border-box;
}

body {
    margin: 0;
}

.nav {
    background-color: white;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.083);
}

.site-title {
    font-size: 2rem;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

.nav a {
color: black;
text-decoration: none;
height: 100%;
display: flex;
align-items: center;
padding: .25rem;
}



